.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 320px;
  background: linear-gradient(180deg, #0099AE 0%, #00BF82 100%);
  padding-top: 110px;
  position: absolute;
  left: 0;
  top: 0;
  color: aliceblue;
  transition: transform 1s;
  z-index: 1;
}
.sidebar_game {
  background: linear-gradient(
    40deg
    ,#ffd86f,#fc6262);
}
.sidebar_hidden {
  transform: translateX(-321px);
}
.sidebar__login {
  margin-bottom: 30px;
  margin-left: 35px;
  font-size: 22px;
  color: aliceblue;
  background: transparent;
  border-color: aliceblue;
  border-radius: 5px;
  width: 250px;
  height: 60px;
  padding-left: 20px;
}
.sidebar__login:focus {
  outline: none;
}