.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 110px;
}
.menu-toggle {
  width: 55px;
  height: 55px;
  background-image: url(./../../assets/Rectangle3.svg);
  background-position: center;
  z-index: 10;
  margin-left: 15px;
}
.menu-toggle_set-hidden {
  background-image: url(./../../assets/Rectangle2.svg);
}
.switch {
  display: flex;
  align-items: center;
  width: 70px;
  height: 30px;
  border-radius: 30px;
  background: linear-gradient(180deg, #0099AE 0%, #00BF82 100%);
  color: aliceblue;
  padding: 0 5px;
  margin-right: 15px;
}
.switch_game {
  background: linear-gradient(
    40deg
    ,#ffd86f,#fc6262);
}
.menu-toggle,
.switch {
  cursor: pointer;
}
.switch__handle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: rgb(250, 246, 247);
  transition: 1s;
}
.switch__handle_game {
  transform: translateX(40px);
}
@keyframes move-left {
  from {filter: opacity(0)} 
  to {filter: opacity(100%)}
}
@keyframes move-right {
  from {filter: opacity(0)} 
  to {filter: opacity(100%)}
}
.switch__text,
.switch__text_game {
  text-transform: uppercase;
  font-size: 12px;
  transition: 1s;
}
.switch__text {
  margin-left: 3px;
  animation: move-right 0.7s;
}
.switch__text_game {
  position: relative;
  left: -20px;
  animation: move-left 0.7s;
}
