.footer {
  margin: 0 15px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__link {
  display: flex;
  align-items: center;
  text-decoration: none;
  transition: 0.5s;
  color: black;
  font-size: 22px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.footer__text {
  margin-left: 5px;
}
.footer__logo {
  height: 40px;
}
.footer__link:hover {
  transform: scale(1.1) translateY(-1px);
}