.main_wrapper {
  position: relative;
}
.main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}
.card-container {
  position: relative;
  height: 268px;
  width: 318px;
  margin: 15px;
  perspective: 25rem;
}
.card {
  position: absolute;
  top: 0;
  left: 0;
  transform-style: preserve-3d;
  transition: 0.5s;
  height: 100%;
  width: 100%;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  transition: 0.5s;
  overflow: hidden;
  text-align: center;
  text-decoration: none;
  font-size: 22px;
  line-height: 140%;
  color: #212529;
  cursor: pointer;
  backface-visibility: hidden;
  border-radius: 5px;
}
.card_train {
  background: linear-gradient(
    180deg
    ,#0099ae,#00bf82 40%,#fff 0,#fff);
}
.card_game {
  background: linear-gradient(
    180deg
    ,#ffd86f,#fc6262 40%,#fff 0,#fff );
}
.card__image_back {
  display: block;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  margin: 40px auto auto;
  border: 10px solid white;
  background-color: white;
}
.card__image_front {
  height: 218px;
  width: 318px;
  border-radius: 5px;
}
.card__button {
  position: absolute;
  right: 17px;
  bottom: 14px;
  height: 19px;
  width: 19px;
  background: url(./../../assets/cached-black.png);
}
.card__button:hover {
  background: url(./../../assets/cached-green.png);
}
.flipped > .card_eng {
  transform: rotateY(180deg);
}
.flipped > .card_ru {
  transform: rotateY(0deg);
}
.card_ru {
  transform: rotateY(-180deg);
}
.card_eng:hover,
.card_train:hover,
.card_game:hover {
  box-shadow: 0 10px 20px rgb(0 0 0 / 40%);
}
.card_guessed {
  filter: sepia();
  pointer-events: none;
}
.main__button {
  position: absolute;
  right: 100px;
  top: -90px;
  height: 72px;
  width: 72px;
  border-radius: 50%;
  border:none;
  background-image: url('./../../assets/start.png');
  background-position: center;
  background-size: cover;
  cursor: pointer;
  transition: 0.5s;
}
.main__button_repeat {
  background-image: url('./../../assets/restart.png');
}

.main__button_push {
  transform: scale(0.95) translateY(3px);
}
.hidden {
  display: none;
}
@keyframes grow {
  from {transform: scale(2);} 
  to {transform: scale(1);}
}
.win-lose-image_wrapper {
  height: 90vh;
  width: 100vw;
}
.win-lose-image {
  display: block;
  position: fixed;
  top: 20%;
  margin: auto;
  height: 50%;
  width: 50%;
  animation: grow 2s;
}
.win-lose-image__text {
  position: fixed;
  top: 0;
  left: 25%;
  font-size: 5em;
  color: #ff6fd4;
}
@media screen and (max-width: 712px) {
  .win-lose-image__text  {
    font-size: 3em;
    top: 5%;
  }
  .win-lose-image {
    top: 25%;
    left: 15%;
  }
}
.star-container {
  position: absolute;
  top: -20px;
  right: 5px;
  margin-right: 15px;
  max-width: 310px;
  height: 34px;
  overflow: hidden;
}
.star-container_wrapper {
  height: 34px;
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap;
}